/* Reset/Global styles */
body {
  margin: 0;
  font-family: "Segoe UI", sans-serif;
  background-color: #f4f4f4;
}

/* Top Navbar */
.top-navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #1da1f2;
  color: white;
  padding: 0.75rem 1.5rem;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 60px;
  z-index: 1000;
}

.navbar-title {
  color: white;
  font-size: 1.5rem;
  text-decoration: none;
  margin-left: 30px;
}

.top-navbar-right .navbar-link {
  color: white;
  margin-left: 15px;
  text-decoration: none;
}

/* Sidebar */
.sidebar_open {
  background-color: white;
  width: 220px;
  height: calc(100vh - 60px);
  position: fixed;
  top: 60px;
  left: 0;
  padding-top: 20px;
  box-shadow: 2px 0 5px rgba(0, 0, 0, 0.1);
  transform: translateX(0);
  transition: transform 0.3s ease-in-out;
  z-index: 999;
  overflow-y: auto;
}

.sidebar {
  width: 220px;
  background-color: #20232a;
  height: 100vh;
  position: fixed;
  top: 60px;
  left: 0;
  display: flex;
  flex-direction: column;
  padding: 1rem 0;
  box-shadow: 2px 0 5px rgba(0, 0, 0, 0.1);
}

.sidebar-buttons {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  padding: 0 1rem;
}

.sidebar-button {
  background-color: #444;
  color: white;
  border: none;
  padding: 0.75rem 1rem;
  font-size: 1rem;
  text-align: left;
  cursor: pointer;
  transition: background-color 0.2s ease;
  width: 100%;
}

.sidebar-button:hover {
  background-color: #555;
}

/* Main content area */
.main-content {
  margin-left: 220px;
  margin-top: 60px;
  padding: 2rem;
  padding-top: 80px;
  transition: margin-left 0.3s ease;
}

/* Profile Box */
.profile-box {
  background-color: white;
  padding: 1.5rem;
  border-radius: 10px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  margin-bottom: 2rem;
}

/* Profile image */
.profile-image-container {
  display: flex;
  justify-content: center;
  margin-bottom: 1rem;
}

.profile-image {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  object-fit: cover;
  border: 2px solid #ccc;
}

/* Form styling */
.profile-form .ant-form-item-label {
  font-weight: bold;
}

.form-buttons {
  display: flex;
  gap: 1rem;
}

/* Card grid container */
.card-grid {
  display: flex;
  flex-wrap: nowrap;               /* force one-line layout */
  gap: 1.5rem;
  overflow-x: auto;                /* enable horizontal scroll */
  padding-bottom: 1rem;
  -webkit-overflow-scrolling: touch;
  scroll-behavior: smooth;
}

/* Individual Card */
.info-card {
  background-color: white;
  padding: 1.5rem;
  border-radius: 10px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  text-align: center;
  cursor: pointer;
  transition: transform 0.2s ease;
  flex: 0 0 auto;                  /* do not grow/shrink, keep width fixed */
  width: 350px;
  box-sizing: border-box;
  height: 200px;
  margin-left: 10px;
}

.info-card:hover {
  transform: translateY(-3px);
}

.card-icon {
  font-size: 2rem;
  color: #1890ff;
  margin-bottom: 0.5rem;
}

.card-count {
  font-size: 1.25rem;
  font-weight: bold;
}

/* Chart container */
.chart-container {
  background-color: white;
  padding: 1.5rem;
  border-radius: 10px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  margin-top: 2rem;
}

/* Responsive layout */
@media (min-width: 992px) {
  .sidebar-toggle-button {
    display: none;
  }
}

@media (max-width: 991px) {
  .sidebar {
    position: relative;
    width: 100%;
    height: auto;
    flex-direction: row;
    padding: 0.5rem;
    justify-content: space-around;
  }

  .main-content {
    margin: 0;
    padding: 1rem;
    padding-top: 80px;
  }

  .profile-box,
  .chart-container {
    width: 100%;
  }

  .form-buttons {
    flex-direction: column;
  }
}

@media (max-width: 576px) {
  .card-icon {
    font-size: 1.5rem;
  }

  .card-count {
    font-size: 1rem;
  }

  .sidebar {
    flex-wrap: wrap;
  }

  .sidebar-button {
    flex: 1 0 45%;
    margin: 0.25rem;
  }

  .sidebar_open {
    transform: translateX(-100%);
    width: 260px;
    box-shadow: 4px 0 10px rgba(0, 0, 0, 0.15);
  }

  .sidebar_open.open {
    transform: translateX(0);
  }

  .info-card {
    width: 250px;
  }
}
